"use client";
import { useEffect, useState } from "react";
import { useSessionStore } from "@/store/sessionStore";

interface FloatingEmoji {
  id: number;
  emoji: string;
  left: number; // Random horizontal position
}

export default function ReactionOverlay() {
  const { lastReaction } = useSessionStore();
  const [emojis, setEmojis] = useState<FloatingEmoji[]>([]);

  useEffect(() => {
    if (!lastReaction) return;
    
    const emojiChar = lastReaction.split("-")[0];
    const newEmoji = {
      id: Date.now(),
      emoji: emojiChar,
      left: Math.random() * 90 // Random % from left (0-90)
    };

    setEmojis((prev) => [...prev, newEmoji]);

    // Cleanup after animation (2 seconds)
    setTimeout(() => {
      setEmojis((prev) => prev.filter(e => e.id !== newEmoji.id));
    }, 2000);

  }, [lastReaction]);

  return (
    <div className="fixed inset-0 pointer-events-none overflow-hidden z-50">
      {emojis.map((e) => (
        <div
          key={e.id}
          className="absolute bottom-0 text-4xl animate-float-up opacity-0"
          style={{ left: `${e.left}%` }}
        >
          {e.emoji}
        </div>
      ))}
      <style jsx>{`
        @keyframes float-up {
          0% { transform: translateY(0) scale(0.5); opacity: 1; }
          100% { transform: translateY(-80vh) scale(1.5); opacity: 0; }
        }
        .animate-float-up {
          animation: float-up 2s ease-out forwards;
        }
      `}</style>
    </div>
  );
}