import { useEffect, useRef } from 'react';
import { useSessionStore } from '@/store/sessionStore';

export function useRealtime(sessionCode: string) {
  const ws = useRef<WebSocket | null>(null);

  useEffect(() => {
    if (!sessionCode) return;

    const connect = () => {
      // Use standard WebSocket URL format
      // Note: In a real app, you might want separate URLs for presenter/audience to distinguish them, 
      // but for this MVP, the single endpoint works for receiving broadcasts.
      const wsUrl = `ws://localhost:8001/ws/${sessionCode}/presenter`; 
      
      ws.current = new WebSocket(wsUrl);

      ws.current.onopen = () => {
        console.log('âœ… WebSocket Connected');
        useSessionStore.getState().setConnectionStatus(true);
        
        // Initial Fetch to sync state (Polls, Q&A, Participants, Quiz, Locations)
        fetch(`http://localhost:8001/api/session/${sessionCode}/state`)
          .then(res => res.json())
          .then(data => {
            if(data.current_poll) useSessionStore.getState().setPoll(data.current_poll);
            if(data.questions) useSessionStore.getState().setQuestions(data.questions);
            if(data.participants) useSessionStore.getState().setParticipants(data.participants);
            
            // Sync Quiz State if active
            if(data.quiz) {
                useSessionStore.getState().setQuiz(data.quiz, data.quiz_scores);
            }

            // Sync Locations (Magic Map) if present
            if(data.locations) {
                useSessionStore.getState().setLocations(data.locations);
            }
          })
          .catch(err => console.error("State sync failed", err));
      };

      ws.current.onmessage = (event) => {
        const data = JSON.parse(event.data);
        console.log("ðŸ“© WS Msg:", data.type);

        switch (data.type) {
          case 'POLL_START':
          case 'POLL_UPDATE':
            useSessionStore.getState().setPoll(data.payload);
            break;
            
          case 'QNA_UPDATE':
            useSessionStore.getState().setQuestions(data.payload);
            break;

          case 'PARTICIPANT_UPDATE':
            useSessionStore.getState().setParticipants(data.names);
            // UPDATE: Handle new Magic Map locations if sent
            if (data.locations) {
                useSessionStore.getState().setLocations(data.locations);
            }
            break;

          case 'REACTION':
            useSessionStore.getState().triggerReaction(data.emoji);
            break;

          // === NEW QUIZ HANDLER ===
          case 'QUIZ_UPDATE':
            // Backend sends either just { ...quiz } OR { quiz: {...}, scores: {...} }
            if (data.payload.quiz) {
                useSessionStore.getState().setQuiz(data.payload.quiz, data.payload.scores);
            } else {
                useSessionStore.getState().setQuiz(data.payload);
            }
            break;
        }
      };

      ws.current.onclose = () => {
        console.log('âŒ WebSocket Disconnected');
        useSessionStore.getState().setConnectionStatus(false);
        // Simple reconnect logic
        setTimeout(connect, 3000);
      };
    };

    connect();

    return () => {
      ws.current?.close();
    };
  }, [sessionCode]);
}