"use client";
import { useState } from "react";

export default function QuizCreator({ sessionCode, onClose }: { sessionCode: string; onClose: () => void }) {
  const [title, setTitle] = useState("Fun Quiz");
  const [questions, setQuestions] = useState([
    { id: "1", text: "", options: ["", "", "", ""], correct_index: 0, time_limit: 30 }
  ]);
  const [loading, setLoading] = useState(false);

  const updateQuestion = (idx: number, field: string, value: any) => {
    const newQs = [...questions];
    (newQs[idx] as any)[field] = value;
    setQuestions(newQs);
  };

  const updateOption = (qIdx: number, oIdx: number, val: string) => {
    const newQs = [...questions];
    newQs[qIdx].options[oIdx] = val;
    setQuestions(newQs);
  };

  const handleLaunch = async () => {
    setLoading(true);
    await fetch(`http://localhost:8001/api/session/${sessionCode}/quiz/start`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ title, questions })
    });
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black/90 flex items-center justify-center z-50 p-4 overflow-y-auto">
      <div className="bg-slate-900 w-full max-w-2xl p-6 rounded-2xl border border-slate-700">
        <h2 className="text-2xl font-bold mb-4">Create Quiz</h2>
        
        {/* FIXED: Added aria-label for accessibility */}
        <input 
            aria-label="Quiz Title"
            className="w-full bg-slate-800 p-2 rounded mb-4 text-white border border-slate-700 focus:border-blue-500 outline-none" 
            value={title} 
            onChange={e => setTitle(e.target.value)} 
        />
        
        <div className="space-y-6 max-h-[50vh] overflow-y-auto mb-4 pr-2 scrollbar-thin scrollbar-thumb-slate-700">
            {questions.map((q, qIdx) => (
                <div key={qIdx} className="p-4 bg-slate-800 rounded-xl border border-slate-700">
                    <div className="flex justify-between mb-2">
                        <span className="font-bold">Question {qIdx + 1}</span>
                        
                        {/* FIXED: Added aria-label for accessibility */}
                        <select 
                            aria-label="Time limit per question"
                            className="bg-slate-700 rounded px-2 py-1 text-sm outline-none focus:ring-1 focus:ring-blue-500"
                            value={q.time_limit}
                            onChange={(e) => updateQuestion(qIdx, "time_limit", parseInt(e.target.value))}
                        >
                            <option value={10}>10s</option>
                            <option value={20}>20s</option>
                            <option value={30}>30s</option>
                        </select>
                    </div>
                    
                    {/* FIXED: Added aria-label */}
                    <input 
                        aria-label={`Question ${qIdx + 1} text`}
                        className="w-full bg-slate-700 p-2 rounded mb-3 text-white placeholder-slate-400 outline-none focus:ring-1 focus:ring-blue-500" 
                        placeholder="e.g. What is the capital of France?"
                        value={q.text}
                        onChange={(e) => updateQuestion(qIdx, "text", e.target.value)}
                    />
                    
                    <div className="grid grid-cols-2 gap-2">
                        {q.options.map((opt, oIdx) => (
                            <div key={oIdx} className="flex items-center gap-2">
                                {/* FIXED: Added aria-label for radio button */}
                                <input 
                                    type="radio" 
                                    name={`correct-${qIdx}`} 
                                    aria-label={`Mark option ${oIdx + 1} as correct`}
                                    checked={q.correct_index === oIdx}
                                    onChange={() => updateQuestion(qIdx, "correct_index", oIdx)}
                                    className="accent-green-500 w-4 h-4 cursor-pointer"
                                />
                                
                                {/* FIXED: Added aria-label for option text */}
                                <input 
                                    aria-label={`Option ${oIdx + 1} text`}
                                    className="w-full bg-slate-700 p-2 rounded text-sm text-white placeholder-slate-400 outline-none focus:ring-1 focus:ring-blue-500"
                                    placeholder={`Option ${oIdx + 1}`}
                                    value={opt}
                                    onChange={(e) => updateOption(qIdx, oIdx, e.target.value)}
                                />
                            </div>
                        ))}
                    </div>
                </div>
            ))}
        </div>
        
        <button onClick={() => setQuestions([...questions, { id: Date.now().toString(), text: "", options: ["", "", "", ""], correct_index: 0, time_limit: 30 }])} className="text-blue-400 text-sm mb-6 hover:underline">+ Add Question</button>

        <div className="flex gap-4">
            <button onClick={onClose} className="flex-1 py-3 bg-slate-800 rounded hover:bg-slate-700 transition">Cancel</button>
            <button onClick={handleLaunch} disabled={loading} className="flex-1 py-3 bg-green-600 hover:bg-green-500 font-bold rounded transition disabled:opacity-50">
                {loading ? "Starting..." : "Launch Quiz"}
            </button>
        </div>
      </div>
    </div>
  );
}