"use client";
import { useState } from "react";

export default function CreatePollForm({ sessionCode, onClose }: { sessionCode: string; onClose: () => void }) {
  const [question, setQuestion] = useState("");
  const [type, setType] = useState<"multiple_choice" | "rating" | "word_cloud">("multiple_choice");
  const [options, setOptions] = useState(["", ""]);
  const [loading, setLoading] = useState(false);

  const handleSubmit = async () => {
    if (!question) return;

    // Payload construction
    const payload: any = { question, type };
    if (type === "multiple_choice") {
       payload.options = options.filter(o => o.trim() !== "");
       if (payload.options.length < 2) return alert("Add at least 2 options");
    }

    setLoading(true);
    await fetch(`http://localhost:8001/api/session/${sessionCode}/poll/start`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload)
    });
    onClose();
  };

  return (
    <div className="fixed inset-0 bg-black/80 flex items-center justify-center p-4 z-50">
      <div className="bg-slate-900 border border-slate-700 p-6 rounded-2xl w-full max-w-md">
        <h2 className="text-xl font-bold mb-4">Launch Widget</h2>

        {/* Type Selector */}
        <div className="flex gap-2 mb-4">
            {[
                { id: "multiple_choice", label: "ðŸ“Š Poll" },
                { id: "rating", label: "â­ Rating" },
                { id: "word_cloud", label: "â˜ï¸ Word Cloud" }
            ].map((t) => (
                <button
                    key={t.id}
                    onClick={() => setType(t.id as any)}
                    className={`flex-1 py-2 text-sm rounded-lg border ${
                        type === t.id ? "bg-blue-600 border-blue-500" : "bg-slate-800 border-slate-700 text-slate-400"
                    }`}
                >
                    {t.label}
                </button>
            ))}
        </div>
        
        <input 
          className="w-full bg-slate-800 border border-slate-700 p-3 rounded-lg mb-4 text-white"
          placeholder="Ask a question..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
        />

        {/* Dynamic Inputs based on Type */}
        {type === "multiple_choice" && (
          <div className="space-y-2 mb-4">
            {options.map((opt, idx) => (
               <input
                 key={idx}
                 className="w-full bg-slate-800/50 border border-slate-700 p-2 rounded text-sm text-white"
                 placeholder={`Option ${idx + 1}`}
                 value={opt}
                 onChange={(e) => {
                    const newOpts = [...options];
                    newOpts[idx] = e.target.value;
                    setOptions(newOpts);
                 }}
               />
            ))}
            <button onClick={() => setOptions([...options, ""])} className="text-xs text-blue-400">+ Add Option</button>
          </div>
        )}

        {type === "rating" && (
            <div className="p-4 bg-slate-800/50 rounded-lg text-center text-slate-400 text-sm mb-4">
                Participants will see a 5-star input.
            </div>
        )}

        {type === "word_cloud" && (
            <div className="p-4 bg-slate-800/50 rounded-lg text-center text-slate-400 text-sm mb-4">
                Participants will type free text responses.
            </div>
        )}

        <div className="flex gap-3">
          <button onClick={onClose} className="flex-1 py-3 text-slate-400 hover:bg-slate-800 rounded-lg">Cancel</button>
          <button onClick={handleSubmit} disabled={loading} className="flex-1 py-3 bg-blue-600 text-white font-bold rounded-lg">
            {loading ? "Launching..." : "Launch ðŸš€"}
          </button>
        </div>
      </div>
    </div>
  );
}