"use client";
import { useSessionStore } from "@/store/sessionStore";

export default function MagicMap({ onClose }: { onClose: () => void }) {
  const { locations } = useSessionStore();

  // Simple Mercator projection approximation for a standard world map SVG
  const getXY = (lat: number, lng: number) => {
    const x = (lng + 180) * (100 / 360);
    const y = ((-lat + 90) * (100 / 180)); 
    return { x, y };
  };

  return (
    <div className="fixed inset-0 bg-slate-950 z-50 flex flex-col">
      {/* Header */}
      <div className="p-6 flex justify-between items-center border-b border-slate-800 bg-slate-900">
        <h2 className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-500">
            ðŸŒ Magic Map: Live Audience
        </h2>
        <button onClick={onClose} className="px-4 py-2 bg-slate-800 rounded hover:bg-slate-700 text-white">Close Map</button>
      </div>

      {/* Map Container */}
      <div className="flex-1 relative bg-[#0a0f1c] flex items-center justify-center overflow-hidden p-10">
        
        {/* World Map SVG Background (Public Domain) */}
        <div className="relative w-full max-w-6xl aspect-[2/1]">
            <img 
                src="https://upload.wikimedia.org/wikipedia/commons/2/23/Blue_Marble_2002.png" 
                alt="World Map" 
                className="w-full h-full object-cover rounded-2xl opacity-50 shadow-2xl"
            />

            {/* Render Dots */}
            {locations.map((loc, i) => {
                const { x, y } = getXY(loc.lat, loc.lng);
                return (
                    <div 
                        key={i}
                        className="absolute w-4 h-4 bg-green-500 rounded-full shadow-[0_0_10px_#4ade80] border-2 border-white transform -translate-x-1/2 -translate-y-1/2 animate-pulse group cursor-pointer"
                        style={{ left: `${x}%`, top: `${y}%` }}
                    >
                        {/* Tooltip */}
                        <div className="absolute bottom-full left-1/2 -translate-x-1/2 mb-2 px-2 py-1 bg-white text-black text-xs font-bold rounded opacity-0 group-hover:opacity-100 transition whitespace-nowrap pointer-events-none">
                            {loc.name} ({loc.city})
                        </div>
                    </div>
                );
            })}
        </div>
      </div>
      
      {/* Footer Stats */}
      <div className="p-6 bg-slate-900 border-t border-slate-800 flex gap-6 text-slate-400 text-sm">
          <span>ðŸ“ {locations.length} Active Users</span>
          <span>ðŸŒ {new Set(locations.map(l => l.city)).size} Cities</span>
      </div>
    </div>
  );
}