import { create } from 'zustand';

export interface Question {
  id: string;
  text: string;
  votes: number;
}

export interface Poll {
  type: "multiple_choice" | "rating" | "word_cloud";
  question: string;
  is_open: boolean;
  options?: { label: string; votes: number }[];
  average?: number;
  counts?: Record<string, number>;
  words?: Record<string, number>;
}

export interface QuizQuestion {
  id: string;
  text: string;
  options: string[];
  correct_index: number;
  time_limit: number;
}

export interface QuizState {
  title: string;
  questions: QuizQuestion[];
  current_index: number;
  state: "LOBBY" | "QUESTION" | "LEADERBOARD" | "END";
  start_time_epoch: number;
}

// === NEW: Location Interface ===
export interface UserLocation {
  name: string;
  lat: number;
  lng: number;
  city: string;
}

interface SessionState {
  isConnected: boolean;
  participants: string[];
  currentPoll: Poll | null;
  questions: Question[];
  lastReaction: string | null;
  quiz: QuizState | null;
  quizScores: Record<string, number>; 
  
  // === NEW: Location State ===
  locations: UserLocation[];

  setConnectionStatus: (status: boolean) => void;
  setParticipants: (names: string[]) => void;
  setPoll: (poll: Poll) => void;
  setQuestions: (qs: Question[]) => void;
  triggerReaction: (emoji: string) => void;
  setQuiz: (quiz: QuizState | null, scores?: Record<string, number>) => void;
  
  // === NEW: Location Setter ===
  setLocations: (locs: UserLocation[]) => void;
}

export const useSessionStore = create<SessionState>((set) => ({
  isConnected: false,
  participants: [],
  currentPoll: null,
  questions: [],
  lastReaction: null,
  quiz: null,
  quizScores: {},
  locations: [], // <--- Initial State

  setConnectionStatus: (status) => set({ isConnected: status }),
  setParticipants: (names) => set({ participants: names }),
  setPoll: (poll) => set({ currentPoll: poll }),
  setQuestions: (qs) => set({ questions: qs }),
  triggerReaction: (emoji) => set({ lastReaction: `${emoji}-${Date.now()}` }),
  setQuiz: (quiz, scores) => set((state) => ({ 
      quiz, 
      quizScores: scores || state.quizScores 
  })),
  
  // <--- Action
  setLocations: (locs) => set({ locations: locs }),
}));